var index = r => require.ensure([], () => r(require('./pages/index.vue')), 'index');
var about = r => require.ensure([], () => r(require('./pages/about.vue')), 'rolunk');
var articleParty = r => require.ensure([], () => r(require('./pages/article-party.vue')), 'lakodalmas-zenekar');
var articleWedding = r => require.ensure([], () => r(require('./pages/article-wedding.vue')), 'zenekar-eskuvore');
var downloads = r => require.ensure([], () => r(require('./pages/downloads.vue')), 'letoltesek');
var gallery = r => require.ensure([], () => r(require('./pages/gallery.vue')), 'galeria');
var opinions = r => require.ensure([], () => r(require('./pages/opinions.vue')), 'video');
var partners = r => require.ensure([], () => r(require('./pages/partners.vue')), 'partnereink');
var repertoire = r => require.ensure([], () => r(require('./pages/repertoire.vue')), 'repertoar');
var video = r => require.ensure([], () => r(require('./pages/video.vue')), 'velemenyek');

var routes = [
  { 
    path: '/',
    component: index,
    data: {
      linkName: 'Foldal',
      metaDesc: 'Ok Zenekar lakodalom party bl eksv Zenei knlat kompromisszum nlkl!'
    }
  },
  { 
    path: '/rolunk',
    component: about,
    data: {
      linkName: 'Rlunk',
      metaDesc: 'Rlunk: Mi, a zenekar tagjai gyermekkorunk ta zenlnk, tzen ves korunk ta jtszunk tnczent. Mind ketten igazi hangszeres zenszek vagyunk. Hangszereink: szintetiztor, harmonika, szaxofon, klarint. Hangszereink mellett nekhangunkkal is jl bnunk.'
    }
  }
  ,
  { 
    path: '/lakodalmas-zenekar',
    component: articleParty,
    data: {
      category: 'article',
      linkName: 'Lakodalmas Zenekar',
      metaDesc: 'Lakodalmas zenekar ismertet'
    }
  },
  { 
    path: '/zenekar-eskuvore',
    component: articleWedding,
    data: {
      category: 'article',
      linkName: 'Zenekar eskvre',
      metaDesc: 'Zenekar eskvre ismertet'
    }
  },
  { 
    path: '/letoltesek',
    component: downloads,
    data: {
      linkName: 'Letltsek',
      metaDesc: 'Zenk letltse'
    }
  },
  { 
    path: '/galeria',
    component: gallery,
    data: {
      linkName: 'Galria',
      metaDesc: 'Ftk a zenekar tagjairl, bulikrl, rendezvnyekrl.'
    }
  },
  { 
    path: '/video',
    component: video,
    data: {
      linkName: 'Vide',
      metaDesc: 'Videink'
    }
  },
  { 
    path: '/partnereink',
    component: partners,
    data: {
      linkName: 'Partnereink',
      metaDesc: 'Partnereink'
    }
  },
  { 
    path: '/repertoar',
    component: repertoire,
    data: {
      linkName: 'Repertor',
      metaDesc: 'Repertorunk'
    }
  },
  { 
    path: '/velemenyek',
    component: opinions,
    data: {
      linkName: 'Vlemnyek',
      metaDesc: 'Vlemnyek a zenekarrl'
    }
  }
]

//console.log(routes);

module.exports = routes;


